.lighter-text {
  color: #abb0be;
}

.main-color-text {
  color: #6394f8;
}

nav {
  /* padding: 20px 0 40px 0; */
  background: #f8f8f8;
  font-size: 16px;
}

li {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

.badge {
  background-color: #6394f8;
  border-radius: 10px;
  color: white;
  display: inline-block;
  font-size: 12px;
  line-height: 1;
  padding: 3px 7px;
  text-align: center;
  vertical-align: middle;
  white-space: nowrap;
}

.shopping-cart {
  margin: 20px 0;
  float: left;
  background: white;
  width: 320px;
  position: relative;
  border-radius: 3px;
  padding: 20px;
}

.shopping-cart-header {
  border-bottom: 1px solid #e8e8e8;
  padding-bottom: 15px;
}
.shopping-cart-total {
  float: right;
}

.item {
  background: white;
  color: 6394f8;
  text-align: left;
  padding: 12px;
  /* text-decoration: none; */
  display: block;
  border-radius: 3px;
  font-size: 14px;
  margin: 25px 0 15px 0;
}

