.loading {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  padding: 20px 50px;
  white-space: nowrap;
  border-left: 5px solid #6394f8;
  border-right: 5px solid #6394f8;
  border-bottom: 5px solid #6394f8;
}
.loading-letter {
  list-style: none;
  position: relative;
  color: #6394f8;
  display: inline-block;
  font-size: 40px;
  text-transform: uppercase;
  font-weight: bold;
  -webkit-animation: animate 1.5s infinite linear;
          animation: animate 1.5s infinite linear;
}
.loading-letter:nth-of-type(4) {
  color: rgb(216, 162, 12);
}
@-webkit-keyframes animate {
  0% {
    -webkit-transform: translateY(0) rotate(0deg);
            transform: translateY(0) rotate(0deg);
  }
  25% {
    -webkit-transform: translateY(-100px) rotate(180deg);
            transform: translateY(-100px) rotate(180deg);
  }
  50% {
    -webkit-transform: translateY(0) rotate(360deg);
            transform: translateY(0) rotate(360deg);
  }
  100% {
    -webkit-transform: translateY(0) rotate(360deg);
            transform: translateY(0) rotate(360deg);
  }
}
@keyframes animate {
  0% {
    -webkit-transform: translateY(0) rotate(0deg);
            transform: translateY(0) rotate(0deg);
  }
  25% {
    -webkit-transform: translateY(-100px) rotate(180deg);
            transform: translateY(-100px) rotate(180deg);
  }
  50% {
    -webkit-transform: translateY(0) rotate(360deg);
            transform: translateY(0) rotate(360deg);
  }
  100% {
    -webkit-transform: translateY(0) rotate(360deg);
            transform: translateY(0) rotate(360deg);
  }
}
.loading-letter:nth-of-type(1) {
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
}
.loading-letter:nth-of-type(2) {
  -webkit-animation-delay: 0.1s;
          animation-delay: 0.1s;
}
.loading-letter:nth-of-type(3) {
  -webkit-animation-delay: 0.2s;
          animation-delay: 0.2s;
}
.loading-letter:nth-of-type(4) {
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
}
.loading-letter:nth-of-type(5) {
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
}
.loading-letter:nth-of-type(6) {
  -webkit-animation-delay: 0.1s;
          animation-delay: 0.1s;
}
.loading-letter:nth-of-type(7) {
  -webkit-animation-delay: 0.2s;
          animation-delay: 0.2s;
}

